<template>
  <div>
    <div class="sdheaderdf">
      <div class="sdlh">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>数据管理</el-breadcrumb-item>
          <el-breadcrumb-item>用户列表</el-breadcrumb-item>
        </el-breadcrumb>
      </div>

      <div class="sdimg">
        <el-menu
          :default-active="activeIndex"
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
          background-color="#eff2f7"
          text-color="#000"
          active-text-color="#ffd04b"
        >
          <el-submenu index="6">
            <template slot="title">
              <img class="sdimage" src="//elm.cangdu.org/img/default.jpg" alt />
            </template>
            <el-menu-item index="6-1">修改</el-menu-item>
            <el-menu-item index="6-2" @click="open">退出</el-menu-item>
          </el-submenu>
        </el-menu>
      </div>
    </div>

    <!-- ================================================================ -->
    <div class="sdpadding">
      <el-table :data="tableData" height="800"  style="width: 100%">
        <el-table-column prop="user_name" label="姓名"></el-table-column>
        <el-table-column prop="create_time" label="注册日期"></el-table-column>
        <el-table-column prop="city" label="地址"></el-table-column>
        <el-table-column prop="admin" label="权限"></el-table-column>
      </el-table>
    </div>
    <div class="block">
      <!-- <span class="demonstration">完整功能</span> -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 5, 10, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      tableData: [],
      activeIndex: "1",
      queryInfo: {
        pagenum: 1,
        pagesize: 20
      },
      total: 20000
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {
    this.getuserList();
    //
  },
  // 组件方法
  methods: {
    async getuserList() {
      // /admin/all?offset=0&limit=20
      let { data: res } = await this.$http.get(
        `/admin/all?offset=${this.queryInfo.pagenum}&limit=${this.queryInfo.pagesize}`
      );
      this.tableData = res.data
      // this.total=res
      console.log(this.tableData);
    },

    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getuserList();
      // console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getuserList();
      // console.log(`当前页: ${val}`);
    },
    open() {
      this.$confirm("确定要退出吗", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          sessionStorage.removeItem("token");
          this.$router.push("/Login");
          this.$message({
            type: "success",
            message: "退出成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消退出"
          });
        });
    }
  },
  // 以下是生命周期钩子 注：没用到的钩子请自行删除
  /**
   * 在实例初始化之后，组件属性计算之前，如data属性等
   */
  beforeCreate() {},

  /**
   * 在挂载开始之前被调用：相关的 render 函数首次被调用。
   */
  beforeMount() {},
  /**
   * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
   * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
   */
  mounted() {},
  /**
   * 数据更新时调用，发生在虚拟 DOM 重新渲染和打补丁之前。
   * 你可以在这个钩子中进一步地更改状态，这不会触发附加的重渲染过程。
   */
  beforeUpdate() {},
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {},
  /**
   * 实例销毁之前调用。在这一步，实例仍然完全可用。
   */
  beforeDestroy() {},
  /**
   * Vue 实例销毁后调用。调用后，Vue 实例指示的所有东西都会解绑定，
   * 所有的事件监听器会被移除，所有的子实例也会被销毁。
   */
  destroyed() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped>
.sdheaderdf {
  width: 100%;
  background-color: #eff2f7;
  display: flex;
  justify-content: space-between;
}
.sdimage {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  margin-right: -30px;
}

.sdheaderdf_p {
  color: #97a8be;
  font-size: 12px;
  line-height: 71px;
  margin-left: 30px;
}
.sdlh {
  padding: 20px;
}
.sdpadding {
  margin: 20px;
  border: 1px solid #ddd;
}
</style>
