import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
// import store from './store'
import Login from '@/components/Login.vue'
import Shouye from '../views/Shouye.vue'
import UserList from '../views/UserList.vue'
import ShopList from '../views/ShopList.vue'
import FoodList from '../views/FoodList.vue'
import OrderList from '../views/OrderList.vue'
import AdminList from '../views/AdminList.vue'
import AddShop from '../views/AddShop.vue'
import AddGoods from '../views/AddGoods.vue'
import Visitor from '../views/Visitor.vue'
import VueEdit from '../views/VueEdit.vue'
import AdminSet from '../views/AdminSet.vue'
import Explain from '../views/Explain.vue'





// import Xiangce from '../views/Xiangce.vue'


// // 页面刷新时，重新赋值token
// if (sessionStorage.getItem('token')) {
//   store.commit('set_token', sessionStorage.getItem('token'))
//   }
Vue.use(VueRouter)

const routes = [

  {
    path: '/',
    redirect:'Login'
  },
  {
    path: '/Login',
    name: 'Login',
    component: Login
  },
  {
    path: '/home',
    name: 'Home',
    component: Home,
    children:[
      {
        path: 'shouye',
        name: 'Shouye',
        component: Shouye,
      },
      {
        path: 'userList',
        name: 'UserList',
        component: UserList,
      },
      {
        path: 'shoplist',
        name: 'ShopList',
        component: ShopList,
      },
      {
        path: 'foodList',
        name: 'FoodList',
        component: FoodList,
      },
      {
        path: 'orderList',
        name: 'OrderList',
        component: OrderList,
      },
      {
        path: 'adminList',
        name: 'AdminList',
        component: AdminList,
      },
      {
        path: 'addShop',
        name: 'AddShop',
        component: AddShop,
      },
      {
        path: 'addGoods',
        name: 'AddGoods',
        component: AddGoods,
      },
      {
        path: 'visitor',
        name: 'Visitor',
        component: Visitor,
      },
      {
        path: 'vueEdit',
        name: 'VueEdit',
        component: VueEdit,
      },
      {
        path: 'adminSet',
        name: 'AdminSet',
        component: AdminSet,
      },
      {
        path: 'explain',
        name: 'Explain',
        component: Explain,
      },
      
    ],
    beforeEnter:(to,from,next)=>{
      if(window.sessionStorage.getItem("token")){
        next()
      }else{
        alert("请先登录");
        next("/Login")
      }
    },
  },






]

const router = new VueRouter({
  routes
})

export default router
