<template>
  <div class="sdboxbg">
    <!-- <h1>login登录</h1> -->

    <div class="sdbox">
      <div class="sdheader">
        <div class="sdrow">
          <p>elm后台管理系统</p>
        </div>
      </div>
      <!-- 登录页面 -->
      <div class="sdinput">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
          <el-form-item prop="user_name">
            <el-input placeholder="用户名" v-model.trim="ruleForm.user_name"></el-input>
          </el-form-item>
        </el-form>

        <!-- ============================================== -->
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
          <el-form-item prop="password">
            <el-input type="password" placeholder="密码" v-model.trim="ruleForm.password"></el-input>
          </el-form-item>
        </el-form>

        <!-- 按钮 -->
        <div class="sdann">
          <button type="primary" @click="Login()">登录</button>
        </div>
        <div class="sdzhushi">
          <p>温馨提示：</p>
          <p>未登录过的新用户，自动注册</p>
          <p>注册过的用户可凭账号密码登录</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      list: "",
      ruleForm: {
        user_name: "",
        password: ""
      },
      rules: {
        user_name: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 18, message: "长度在 3 到 18 个字符", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 3, max: 20, message: "长度在 3 到 20 个字符", trigger: "blur" }
        ]
      }
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    async Login() {
      let { data: res } = await this.$http.post("/admin/login", this.ruleForm);
      console.log(res);
      if (res.status == 1) {
        this.$message({
          message: "登录成功",
          type: "success"
        });
        sessionStorage.setItem("token", res.status.token);
        this.$router.push('/home/Shouye');
      }
    }
  },
  mounted() {
    this.Login();
  },
  // 以下是生命周期钩子 注：没用到的钩子请自行删除
  /**
   * 在实例初始化之后，组件属性计算之前，如data属性等
   */
  beforeCreate() {},
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
   * 在挂载开始之前被调用：相关的 render 函数首次被调用。
   */
  beforeMount() {},
  /**
   * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
   * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
   */
  /**
   * 数据更新时调用，发生在虚拟 DOM 重新渲染和打补丁之前。
   * 你可以在这个钩子中进一步地更改状态，这不会触发附加的重渲染过程。
   */
  beforeUpdate() {},
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {},
  /**
   * 实例销毁之前调用。在这一步，实例仍然完全可用。
   */
  beforeDestroy() {},
  /**
   * Vue 实例销毁后调用。调用后，Vue 实例指示的所有东西都会解绑定，
   * 所有的事件监听器会被移除，所有的子实例也会被销毁。
   */
  destroyed() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped>
* {
  margin: 0;
  padding: 0;
}
.sdboxbg {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #324057;
  background-size: 100% 100%;
}

.sdbox {
  width: 370px;
  height: 270px;
  margin: 0 auto;
  border-radius: 5px;
  margin-top: 350px;
  background: #ffffff;
}
.sdheader {
  text-align: center;
  font-size: 30px;
  padding: 10px;
  position: relative;
}
.sdrow {
  position: absolute;
  left: 0;
  right: 0;
  top: -100px;
  color: #ffffff;
}
.sdpad {
  padding: 20px;
}
button {
  margin: 14px;
  width: 94%;
  background-color: #20a0ff;
  border-color: #20a0ff;
  padding: 6px;
  border-radius: 4px;
  border: none;
  color: #ffffff;
}
.el-form-item {
  list-style: none;
  margin-bottom: 10px;
}
.el-input {
  width: 94%;
  padding: 10px;
}
.sdzhushi {
  text-align: center;
  color: red;
  font-size: 12px;
}

</style>
