<template>
  <div>
    <div class="sdheaderdf">
      <div>
        <p class="sdheaderdf_p">
          <router-link tag="p" to="/home/shouye">首页</router-link>
        </p>
      </div>
      <div class="sdimg">
        <el-menu
          :default-active="activeIndex"
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
          background-color="#eff2f7"
          text-color="#000"
          active-text-color="#ffd04b"
        >
          <el-submenu index="6">
            <template slot="title">
              <!-- <img src="@/assets/logo.png" alt class="sdimg" /> -->
              <img class="sdimage" src="//elm.cangdu.org/img/default.jpg" alt />
            </template>
            <el-menu-item index="6-1">修改</el-menu-item>
            <el-menu-item index="6-2" @click="open">退出</el-menu-item>
          </el-submenu>
        </el-menu>
      </div>
    </div>
    <div class="sdheader">
      <p>数据统计</p>
      <div class="sdoang">
        <ul>
          <li class="sdO">当日数据:</li>
          <li class="sdddd">
            <span>9</span>新增用户
          </li>
          <li class="sdddd">
            <span>1</span>新增订单
          </li>
          <li class="sdddd">
            <span>31</span>新增管理员
          </li>
        </ul>
        <ul>
          <li class="sd1">总数据:</li>
          <li class="sdddd">
            <span>52521</span>注册用户
          </li>
          <li class="sdddd">
            <span>19648</span>订单
          </li>
          <li class="sdddd">
            <span>81503</span>管理员
          </li>
        </ul>
      </div>
    </div>
    <!-- ========================================================= -->
    <!-- <el-card class="box-card"> -->
    <div class="text item">
      <div id="echart" style="height:400px;"></div>
    </div>
    <!-- </el-card> -->
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
       activeIndex: "1"
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
     handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
     open() {
      this.$confirm("确定要退出吗", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          sessionStorage.removeItem("token");
          this.$router.push("/Login");
          this.$message({
            type: "success",
            message: "退出成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消退出"
          });
        });
    },
    setEchart() {
      let myechart = this.$echarts.init(document.getElementById("echart"));
      let option = {
        title: {
          text: "走势图"
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: ["新注册用户", "新增订单", "新增管理员"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: [
            "2020-10-30",
            "2020-10-31",
            "2020-11-01",
            "2020-11-02",
            "2020-11-03",
            "2020-11-04",
            "2020-11-05"
          ]
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "新注册用户",
            type: "line",
            stack: "总量",
            data: [21, 13, 10, 15, 57, 26, 9]
          },
          {
            name: "新增订单",
            type: "line",
            stack: "总量",
            data: [21, 3, 9, 7, 12, 15, 1]
          },
          {
            name: "新增管理员",
            type: "line",
            stack: "总量",
            data: [50, 27, 23, 45, 68, 62, 31]
          }
        ]
      };

      myechart.setOption(option);
    }
  },
  // 以下是生命周期钩子 注：没用到的钩子请自行删除
  /**
   * 在实例初始化之后，组件属性计算之前，如data属性等
   */
  beforeCreate() {},
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  /**
   * 在挂载开始之前被调用：相关的 render 函数首次被调用。
   */
  beforeMount() {},
  /**
   * el 被新创建的 vm.$ el 替换，并挂载到实例上去之后调用该钩子。
   * 如果 root 实例挂载了一个文档内元素，当 mounted 被调用时 vm.$ el 也在文档内。
   */
  mounted() {
    this.setEchart();
  },
  /**
   * 数据更新时调用，发生在虚拟 DOM 重新渲染和打补丁之前。
   * 你可以在这个钩子中进一步地更改状态，这不会触发附加的重渲染过程。
   */
  beforeUpdate() {},
  /**
   * 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
   * 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。
   */
  updated() {},
  /**
   * keep-alive 组件激活时调用。 仅针对keep-alive 组件有效
   */
  activated() {},
  /**
   * keep-alive 组件停用时调用。 仅针对keep-alive 组件有效
   */
  deactivated() {},
  /**
   * 实例销毁之前调用。在这一步，实例仍然完全可用。
   */
  beforeDestroy() {},
  /**
   * Vue 实例销毁后调用。调用后，Vue 实例指示的所有东西都会解绑定，
   * 所有的事件监听器会被移除，所有的子实例也会被销毁。
   */
  destroyed() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!--使用了scoped属性之后，父组件的style样式将不会渗透到子组件中，-->
<!--然而子组件的根节点元素会同时被设置了scoped的父css样式和设置了scoped的子css样式影响，-->
<!--这么设计的目的是父组件可以对子组件根元素进行布局。-->
<style scoped>
* {
  margin: 0;
  padding: 0;
}
.sdheader {
  width: 100%;
  padding: 10px;
}
.sdheader > p {
  padding: 0;
  font-size: 30px;
}

ul {
  width: 100%;
  padding: 5px 10px;
  border-radius: 5px;
  display: flex;
}
li {
  width: 20%;
}
.sdO {
  background: orange;
  font-size: 22px;
  border-radius: 5px;
  margin: 5px;
}
.sd1 {
  background: #20a0ff;
  font-size: 22px;
  border-radius: 5px;
  margin: 5px;
}
.sdddd {
  background: #ddd;
  font-size: 14px;
  border-radius: 5px;
  margin: 5px;
}
.sdddd > span {
  font-size: 35px;
}
.text {
  padding: 40px;
}
.sdheaderdf {
  width: 100%;
  background-color: #eff2f7;
  display: flex;
  justify-content: space-between;
}
.sdimage {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  margin-right: -30px;
}

.sdheaderdf_p {
  color: #97a8be;
  font-size: 12px;
line-height: 71px;
margin-left: 30px;
}
</style>
